<template>
  <form>
    <v-card color="#F3F3F3" elevation="0" rounded="0"
            style="border: 10px solid #000000; font-family: 'Century Gothic',serif">
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
            style="padding: 30px; background: #FFFFFF; border: 10px solid #000000; font-size: 40px"
            color="#C2D3EF"
            text
            @click="doSomething()"
        >
          X
        </v-btn>
      </v-card-actions>
      <div>
        <div style="color: black; font-size: 40px; text-align: center">
            
        </div>
        <div style="color: black; font-size: 40px; text-align: center">
            
        </div>
      </div>
      <v-card rounded="0" color="#F3F3F3" elevation="0" width="50%"
              style="margin-left: 25%; margin-top: 1%; font-size: 25px">
        <p class="nameP">
           
        </p>
        <v-text-field
            @input="$v.name.$touch()"
            @blur="$v.name.$touch()"
            v-model="name"
            placeholder="      "
        ></v-text-field>
        <p class="nameP">
           
        </p>
        <v-text-field
            @input="$v.surname.$touch()"
            @blur="$v.surname.$touch()"
            v-model="surname"
            placeholder="      "
        ></v-text-field>
        <p class="nameP">
            
        </p>
        <v-text-field
            @input="$v.card.$touch()"
            @blur="$v.card.$touch()"
            v-model="card"
            v-mask="'#### #### #### ####'"
            placeholder="      **** **** **** ****"
        ></v-text-field>
        <v-row>
          <v-col cols="5">
            <v-card-text
                style="color: black; margin-top: 4%; margin-left: 8%; font-family: 'Century Gothic',serif; font-size: 30px;">
               :
            </v-card-text>
          </v-col>
          <v-col cols="6">
            <v-select
                @input="$v.place.$touch()"
                @blur="$v.place.$touch()"
                v-model="place"
                :items="testItems"
                style="margin-top: 2.5%; font-size: 20px"
            />
          </v-col>
        </v-row>
      </v-card>
      <v-btn elevation="0"
             @click="submit()"
             color="#C2D3EF" width="20%" height="50px"
             style="margin-left: 40%; margin-top: 3%; margin-bottom: 2%; font-size: 30px">
        
      </v-btn>
    </v-card>
  </form>
</template>

<script>
import {validationMixin} from "vuelidate";
import {required} from "vuelidate/lib/validators";
import axios from "axios";
import VueCookies from "vue-cookies";

export default {
  mixins: [validationMixin],
  validations: {
    name: {required},
    surname: {required},
    card: {required},
    place: {required},
  },
  name: "AddMember",
  data: () => ({
    testItems: [" ", " 1", " 2", " 3"],
    name: '',
    surname: '',
    card: '',
    place: '',
  }),
  methods: {
    doSomething() {
      this.$emit('updateParent', {
        dialog: false,
      })
    },
    submit() {
      this.$v.$touch()
      if (this.valid) {
        if (this.name !== '' && this.surname !== '' && this.card !== '' && this.place !== '') {
          console.log(this.phone)
          let data = {
            name: this.name,
            surname: this.surname,
            card: this.card,
            place: this.place,
          }
          axios.create({
            baseURL: 'https://tipsnbills.herokuapp.com/',
            headers: {
              'Authorization': 'Bearer ' + VueCookies.get("token")
            }
          }).post('/api/auth/signin', data)
              .then(resp => {
                console.log(resp.data.token)
              })
        }
      }
    },
  }
}
</script>

<style scoped>

</style>