<template>
  <v-dialog v-model="dialog" persistent width="80%" rounded="0">
    <template v-slot:activator="{ on, attrs }">
      <v-btn
          color="#C2D3EF"
          x-large
          elevation="0"
          width="75%"
          v-bind="attrs" v-on="on"
          @click="openWind='addMember'"
          style="font-family: 'Century Gothic',serif; font-size: 15px; margin-left: 10%; color: black; margin-top: 15%; margin-bottom: 6%"
      >
         
      </v-btn>
      <v-btn
          color="#C2D3EF"
          x-large
          elevation="0"
          width="75%"
          v-bind="attrs" v-on="on"
          @click="openWind='addPoint'"
          style="font-family: 'Century Gothic',serif; font-size: 15px; margin-left: 10%; color: black;"
      >
         
      </v-btn>
    </template>
    <AddMember v-if="openWind === 'addMember'" @updateParent="updateDialog"/>
    <AddPoint v-if="openWind === 'addPoint'" @updateParent="updateDialog"/>
  </v-dialog>
</template>

<script>
import AddMember from "./components/AddMember";
import AddPoint from "./components/AddPoint";

export default {
  name: "Dialog",
  components: {AddPoint, AddMember},
  data: () => ({
    dialog: false,
    openWind: ''
  }),
  methods: {
    updateDialog(data) {
      this.dialog = data.dialog
    }
  }
}
</script>

