<template>
  <form>
    <v-card color="#F3F3F3" elevation="0" rounded="0"
            style="border: 10px solid #000000; font-family: 'Century Gothic',serif">
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
            style="padding: 30px; background: #FFFFFF; border: 10px solid #000000; font-size: 40px"
            color="#C2D3EF"
            text
            @click="doSomething()"
        >
          X
        </v-btn>
      </v-card-actions>
      <div>
        <div style="color: black; font-size: 40px; text-align: center">
             
        </div>
        <div style="color: black; font-size: 40px; text-align: center">
           
        </div>
      </div>
      <v-card rounded="0" color="#F3F3F3" elevation="0" width="50%"
              style="margin-left: 25%; margin-top: 3%; font-size: 25px">
        <p class="nameP">
           
        </p>
        <v-text-field
            @input="$v.place_name.$touch()"
            @blur="$v.place_name.$touch()"
            v-model="place_name"
            placeholder="            "
        ></v-text-field>
      </v-card>
      <v-btn elevation="0"
             @click="submit()"
             color="#C2D3EF" width="20%" height="50px"
             style="margin-left: 40%; margin-top: 3%; margin-bottom: 5%; font-size: 30px">
        
      </v-btn>
    </v-card>
  </form>
</template>

<script>
import {validationMixin} from "vuelidate";
import {required} from "vuelidate/lib/validators";
import axios from "axios";
import VueCookies from "vue-cookies";

export default {
  mixins: [validationMixin],
  validations: {
    place_name: {required},
  },
  name: "AddPoint",
  data: () => ({
    place_name: '',
  }),
  methods: {
    doSomething() {
      this.$emit('updateParent', {
        dialog: false,
      })
    },
    submit() {
      this.$v.$touch()
      if (this.valid) {
        if (this.place_name !== '') {
          console.log(this.phone)
          let data = {
            place_name: this.place_name,
          }
          axios.create({
            baseURL: 'https://tipsnbills.herokuapp.com/',
            headers: {
              'Authorization': 'Bearer ' + VueCookies.get("token")
            }
          }).post('/api/auth/signin', data)
              .then(resp => {
                console.log(resp.data.token)
              })
        }
      }
    },
  }
}
</script>

